@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@200;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Gluten', cursive, sans-serif;
  font-size: 14px;
  background-color: black;
  overflow: hidden;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  --progressionBarHeight: 40px;
  --progressionBarBorder: 4px;
}
.question {
    text-align: center;
    margin-bottom: 20px;
}

.question p {
    margin-bottom: 20px;
    font-size: 1.3rem;
}

.question img {
    width: 60%;
    border-radius: 15px;
    border: 4px solid wheat;
    margin-bottom: 20px;
}

.question input {
    display: block;
    width: 80%;
    height: 40px;
    margin: 15px auto 30px auto;
    padding: 10px;
    border: 4px solid rgb(73, 69, 69);
    border-radius: 15px;
    font-family: 'Gluten', cursive, sans-serif;
    text-align: center;
}

@media(max-width: 320px) {
    .question {
        margin-bottom: 10px;
    }

    .question p {
        margin-bottom: 10px;
    }

    .question input {
        height: 35px;
        margin: 10px auto 10px auto;
    }
}

@media(max-width: 480px) and (orientation: landscape) {
    .question {
        margin-bottom: 10px;
    }

    .question p {
        font-size: 1.2rem;
        margin-bottom: 5px;
    }

    .question img {
        width: 40%;
        float: left;
    }

    .question input {
        width: 50%;
        margin: 10px auto 15px auto;
    }

    .form::after {
        content: "";
        clear: both;
        display: table;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .question {
        margin-bottom: 10px;
    }

    .question p {
        margin-bottom: 10px;
    }

    .question img {
        width: 45%;
        margin-bottom: 5px;
    }

    .question input {
        width: 70%;
        margin: 5px auto 10px auto;
    }
}
.form {
    text-align: center;
    height: 100%;
    margin-bottom: 20px;
}

.form h2 {
    margin-bottom: 20px;
}

.form button {
    display: inline-block;
    height: 50px;
    width: 50px;
    margin: 0 20px 0 20px;
    border: none;
    border-radius: 50%;
    background-color: wheat;
    color: teal;
    cursor: pointer;
}

.form button:last-child {
    display: block;
    height: 70px;
    width: 70px;
    margin: 20px auto 0 auto;
    color: yellowgreen;
}

.form button.inactive {
    color: rgb(73, 69, 69);
    -webkit-filter: opacity(40%);
            filter: opacity(40%);
}

.svg-inline--fa.fa-w-16 {
    height: 100%;
    width: 95%;
}

@media(max-width: 320px) {
    .form button {
        margin: 0 45px 0 45px;
    }

    .form button:last-child {
        margin: -45px auto 0 auto;
    }
}

@media(min-width: 324px) and (orientation: portrait) {
    .form button {
        margin: 0 80px 0 80px;
    }

    .form button:last-child {
        margin: -55px auto 0 auto;
    }
}

@media(min-width: 360px) and (max-width: 414px) and (orientation: portrait) {
    .form button {
        margin: 0 45px 0 45px;
    }

    .form button:last-child {
        margin: 10px auto 0 auto;
    }
}

@media(max-width: 480px) and (orientation: landscape) {
    .form button {
        margin: 0 40px 0 40px;
    }

    .form button:last-child {
        margin: -10px auto 0 auto;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .form h2 {
        margin-bottom: 15px;
    }

    .form button {
        margin: 0 70px 0 70px;
    }

    .form button:last-child {
        margin: -55px auto 0 auto;
    }
}
.guest {
    text-align: center;
    height: 100%;
    margin-bottom: 20px;
}

.guest h2 {
    margin-bottom: 20px;
}

.guest p:nth-of-type(1) {
    margin-bottom: 20px;
    font-size: 1.3rem;
}

.guest img {
    width: 60%;
    border-radius: 15px;
    border: 4px solid wheat;
    margin-bottom: 10px;
}

.guest p:nth-of-type(2) {
    font-size: 1.5rem;
}

.progress {
    position: relative;
    margin: 50px 20px 0 20px;
    height: var(--progressionBarHeight);
    border: var(--progressionBarBorder) solid rgb(73, 69, 69);
    border-radius: 15px;
    background-color: wheat;
    overflow: hidden;
}

.progress p {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(var(--progressionBarHeight) - 2*(var(--progressionBarBorder)));
    background-color: yellowgreen;
    -webkit-transform: translate(-100%, 0);
            transform: translate(-100%, 0);
    -webkit-animation-name: timeLapse;
            animation-name: timeLapse;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

@-webkit-keyframes timeLapse {
    0% {
        -webkit-transform: translate(-100%, 0);
                transform: translate(-100%, 0);
    }

    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@keyframes timeLapse {
    0% {
        -webkit-transform: translate(-100%, 0);
                transform: translate(-100%, 0);
    }

    100% {
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

@media(max-width: 320px) {
    .progress {
        margin: 20px 20px 0 20px;
    }
}

@media(min-width: 324px) and (orientation: portrait) {
    .guest img {
        width: 50%;
    }

    .progress {
        margin: 50px 60px 0 60px;
    }
}

@media(min-width: 360px) and (orientation: portrait) {
    .guest img {
        width: 60%;
    }

    .progress {
        margin: 50px 40px 0 40px;
    }
}

@media(max-width: 480px) and (orientation: landscape) {
    .guest h2 {
        margin-bottom: 10px;
    }

    .guest p:nth-of-type(1) {
        margin-bottom: 10px;
        font-size: 1.2rem;
    }

    .guest img {
        float: left;
        width: 40%;
        margin-right: 5px;
    }

    .guest p:nth-of-type(2) {
        margin-top: 20px;
        font-size: 1.5rem;
    }

    .progress {
        margin: 50px 25px 0 210px;
    }

    .guest::after {
        content: "";
        clear: both;
        display: block;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .guest p:nth-of-type(1) {
        margin-bottom: 10px;
        font-size: 1.2rem;
    }

    .guest img {
        width: 45%;
    }

    .progress {
        margin: 20px 80px 0 80px;
    }
}
.greetings {
    height: 100%;
}

@media(min-width: 324px) and (orientation: portrait) {
    .greetings {
        font-size: 1.3rem;
    }
}

@media(max-width: 480px) and (orientation: landscape) {
    .greetings {
        font-size: 1.2rem;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .greetings {
        height: 90%;
        font-size: 1.2rem;
    }
}
.info {
    text-align: center;
}

.info p {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 1.2rem;
}

.info button {
    display: inline-block;
    height: 50px;
    width: 50px;
    margin: 0 20px 0 20px;
    border: none;
    border-radius: 25px;
    background-color: wheat;
    cursor: pointer;
}

.svg-inline--fa.fa-w-16 {
    height: 100%;
    width: 95%;
}


.info button:nth-of-type(1) {
    color: tomato;
}

.info button:nth-of-type(2) {
    color: yellowgreen;
}

@media(min-width: 324px) {
    .info p {
        font-size: 1.5rem;
    }
}

@media(max-width: 640px) and (orientation: landscape) {
    .info p {
        margin-top: 0px;
    }

    .info button {
        margin: 0 50px 0 50px;
    }
}
.results {
    height: 100%;
}

.summary {
    display: flex;
    height: 10%;
    margin-bottom: 20px;
    flex-direction: row;
    justify-content: space-around;
}

.summary div {
    height: 100%;

}

.summary div p:nth-child(1) {
    display: inline-block;
    margin-right: 10px;
    height: 100%;
    vertical-align: middle;
    background-color: wheat;
    border-radius: 50%;
    border: 4px solid wheat;
    text-align: center;
}

.summary div p:nth-child(2) {
    display: inline-block;
    vertical-align: middle;
    font-size: 2rem;
}

.summary div:nth-child(1) p:first-child {
    color: tomato;
}

.summary div:nth-child(2) p:first-child {
    color: yellowgreen;
}


.answers {
    height: 65%;
    overflow-y: auto;
    border-top: 4px solid wheat;
    border-bottom: 4px solid wheat;
    border-radius: 10px;
}

.answers li {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 5px;
}

.answers li img {
    border-radius: 15px;
    border-width: 4px;
    border-style: solid;
    margin-right: 5px;
}

.answers li p {
    display: inline-block;
    margin-right: 10px;
    font-size: 1.2rem;
}

.answers li p:last-child {
    color: wheat;
}

.results button {
    display: block;
    height: 60px;
    width: 60px;
    margin: 20px auto 20px auto;
    border: none;
    border-radius: 50%;
    background-color: teal;
    color: yellow;
    border: 2px solid wheat;
    cursor: pointer;
}

.results button .svg-inline--fa.fa-w-16 {
    height: 90%;
    width: 65%;
}

@media(max-width: 480px) and (orientation: landscape) {
    .summary {
        height: 15%;
        margin-bottom: 10px;
    }

    .answers {
        height: 50%;
    }

    .results button {
        margin: 10px auto 10px auto;
    }
}
.settings {
    text-align: center;
}

.explanation {
    text-align: justify;
    font-size: 1.2rem;
    margin-top: 10px;
    margin-bottom: 40px;
}

.settings button {
    display: inline-block;
    height: 50px;
    width: 50px;
    margin: 0 20px 0 20px;
    border: none;
    border-radius: 25px;
    background-color: wheat;
    cursor: pointer;
}

.svg-inline--fa.fa-w-16 {
    height: 100%;
    width: 95%;
}


.settings button:nth-of-type(1) {
    color: tomato;
}

.settings button:nth-of-type(2) {
    color: yellowgreen;
}

.timer {
    position: relative;
    display: inline-block;
    margin-top: 30px;
    margin-bottom: 30px;
    height: 40px;
    width: 100%;
}


.icon {
    position: absolute;
    top: 0;
    left: 20px;
    font-size: 2.5rem;
}

.strip {
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(0, 50%);
            transform: translate(0, 50%);
    display: inline-block;
    height: 20px;
    width: 80%;
    background-color: yellowgreen;
    border: 4px solid wheat;
    border-radius: 15px;
    cursor: pointer;
}

.indicator {
    content: "";
    position: absolute;
    top: 0;
    left: 15%;
    height: 30px;
    width: 30px;
    -webkit-transform: translate(0, calc(-50% + 6px));
            transform: translate(0, calc(-50% + 6px));
    border-radius: 50%;
    background-color: teal;
    border: 2px solid wheat;
}

.strip p:nth-child(2) {
    position: absolute;
    top: -30px;
    left: 15%;
    width: 30px;
    font-size: 1.5rem;
}

.strip p:nth-child(3) {
    position: absolute;
    top: -30px;
    left: 45%;
    width: 30px;
    font-size: 1.5rem;
}

.strip p:nth-child(4) {
    position: absolute;
    top: -30px;
    left: 75%;
    width: 30px;
    font-size: 1.5rem;
}

.icon .svg-inline--fa {
    height: 40px;
}

@media(min-width:324px) and (orientation: portrait) {
    .explanation {
        font-size: 1.3rem;
    }

    .settings button {
        margin: 0 40px 0 40px;
    }
}

@media(max-width: 480px) and (orientation: landscape) {

    .settings h2 {
        margin-top: 10px;
    }

    .explanation {
        margin-bottom: 20px;
        font-size: 1.2rem;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .settings button {
        margin: 0 50px 0 50px;
    }
}
.startPage {
    height: 100%;
}

.description p {
    margin-bottom: 40px;
    text-align: justify;
    font-size: 1.2rem;
    line-height: 1.5rem;
}

.buttons {
    text-align: center;
}

.buttons button {
    font-family: 'Gluten', cursive, sans-serif;
    font-size: 1.2rem;
    letter-spacing: 1px;
    color: rgb(73, 69, 69);
    border: none;
    border-radius: 10px;
    transition: 0.2s;
    cursor: pointer;
}

.firstBtnGroup button:hover {
    border: 4px solid rgb(73, 69, 69);
    font-size: 1.5rem;
}

.firstBtnGroup button.choosen {
    border: 4px solid rgb(73, 69, 69);
    font-size: 1.5rem;
}

.firstBtnGroup button {
    height: 40px;
    width: 80%;
    margin-top: 20px;
}

.firstBtnGroup div:nth-child(1) button {
    background-color: yellowgreen;
}

.firstBtnGroup div:nth-child(2) button {
    background-color: khaki;
}

.firstBtnGroup div:nth-child(3) button {
    background-color: tomato;
}

.firstBtnGroup div p {
    margin-top: 10px;
}

.secondBtnGroup {
    display: flex;
    width: 80%;
    margin: 10px auto 10px auto;
    justify-content: center;
    align-items: center;
}

.secondBtnGroup button:first-child {
    height: 50px;
    width: 50px;
    margin-right: 20px;
    background-color: wheat;
    color: rgb(73, 69, 69);
    display: flex;
    justify-content: center;
    align-items: center;
}



.secondBtnGroup button:first-child:hover svg {
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.secondBtnGroup button:last-child {
    height: 80px;
    width: 80%;
    color: wheat;
    background-color: teal;
}

.secondBtnGroup button:last-child:hover {
    border: 4px solid wheat;
    font-size: 2rem;
}

.secondBtnGroup .svg-inline--fa.fa-w-16 {
    height: 100%;
    width: 100%;
}


@-webkit-keyframes rotate {
    from {
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    to {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}


@keyframes rotate {
    from {
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    to {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@media(max-width: 320px) {
    .description p {
        margin-bottom: 0px;
    }

    .firstBtnGroup button {
        height: 35px;
        margin-top: 5px;
    }

    .secondBtnGroup button:last-child {
        height: 60px;
        width: 80%;
    }
}

@media(min-width: 324px) and (orientation: portrait) {
    .description p {
        font-size: 1.3rem;
    }
}

@media(max-width: 480px) and (orientation: landscape) {
    .buttons {
        text-align: left;
    }

    .description p {
        margin-bottom: -5px;
        font-size: 1.2rem;
        line-height: 1.2rem;
    }

    .firstBtnGroup button {
        display: inline-block;
        margin-right: 20px;
        margin-top: 10px;
        height: 35px;
    }

    .firstBtnGroup div p {
        display: inline-block;
    }

    .secondBtnGroup {
        margin: 10px auto 10px 0;
        align-items: flex-start;
    }

    .secondBtnGroup button:last-child {
        height: 50px;
    }
}

@media(max-width: 640px) and (min-width: 482px) and (orientation: landscape) {
    .description p {
        margin-bottom: 5px;
    }

    .firstBtnGroup button {
        margin-top: 10px;
    }

    .secondBtnGroup button:last-child {
        height: 70px;
    }
}
